body {
  margin:  0;
  background: #649BF5;
}
/* Style the header with a grey background and some padding */
.headeer {
  overflow: hidden;
  background-color: #293F85;
  padding: 20px 5px;
  height: 60px;
}

/* Style the header links */
.headeer a {
  float: left;
  color: #FFFFFF;
  text-align: center;
  padding: 12px;
  text-decoration: none;
  font-size: 18px;
  line-height: 25px;
  border-radius: 4px;
}

/* Style the logo link (notice that we set the same value of line-height and font-size to prevent the header to increase when the font gets bigger */
.headeer a.logo {
  font-size: 25px;
  font-weight: bold;
}

/* Change the background color on mouse-over */
.headeer a:hover {
  background-color: #3A579B;
}

/* Style the active/current link*/
.headeer a.active {
  background-color: #649BF5;
  color: white;
}

/* Float the link section to the right */
.headeer-right {
  float: right;
}

.page-content {
  width: 100%;
  margin:  0 auto;
  display: flex;
  display: -webkit-flex;
  justify-content: center;
  -o-justify-content: center;
  -ms-justify-content: center;
  -moz-justify-content: center;
  -webkit-justify-content: center;
  align-items: center;
  -o-align-items: center;
  -ms-align-items: center;
  -moz-align-items: center;
  -webkit-align-items: center;
}
.form-v9-content  {
  background: #3A579B;
  width: 1500px;
  border-radius: 15px;
  -o-border-radius: 15px;
  -ms-border-radius: 15px;
  -moz-border-radius: 15px;
  -webkit-border-radius: 15px;
  margin: 185px 0;
  font-family: 'Nunito', sans-serif;
  color: #fff;
  font-weight: 700;
  font-size: 16px;
  position: relative;
}
.form-v9-content .form-detail {
  padding: 30px 135px 30px 100px;
  position: relative;
}
.form-v9-content .form-detail h2 {
  font-size: 35px;
  text-align: center;
  position: relative;
  padding: 16px 0 13px;
  margin-bottom: 55px;
}
.form-v9-content .form-detail h2::after {
  background: #fff;
  width: 73px;
  height: 3px;
  content: "";
  position: absolute;
  top: 100%;
  left: 50%;
  transform: translateX(-50%);
  -o-transform: translateX(-50%);
  -ms-transform: translateX(-50%);
  -moz-transform: translateX(-50%);
  -webkit-transform: translateX(-50%);
}
.form-v9-content .form-row-total {
  display: flex;
  display: -webkit-flex;
  justify-content: space-between;
  -o-justify-content: space-between;
  -ms-justify-content: space-between;
  -moz-justify-content: space-between;
  -webkit-justify-content: space-between;
}
.form-v9-content .form-row-total ::placeholder {
  color: #FFFFFF;
}
.form-v9-content .form-row {
  width: 45%;
}
.form-v9-content .form-detail .form-row-last {
  text-align: center;
}
.form-v9-content .form-detail .input-text {
  margin-bottom: 45px;
  padding-right: 15px;
}
.form-v9-content .form-detail input {
  width: 100%;
  padding: 14.5px 0px 14.5px 30px;
  border: 2px solid #ccc;
  appearance: unset;
  -moz-appearance: unset;
  -webkit-appearance: unset;
  -o-appearance: unset;
  -ms-appearance: unset;
  outline: none;
  -moz-outline: none;
  -webkit-outline: none;
  -o-outline: none;
  -ms-outline: none;
  border-radius: 27.5px;
  -o-border-radius: 27.5px;
  -ms-border-radius: 27.5px;
  -moz-border-radius: 27.5px;
  -webkit-border-radius: 27.5px;
  font-family: 'Nunito', sans-serif;
  font-size: 16px;
  font-weight: 700;
  background: rgba(255, 255, 255, 0.2)
}
.form-v9-content .form-detail input:focus {
  border: 2px solid #999;
}
.form-v9-content .form-detail .register {
  background: #B6A84D;
  border-radius: 25px;
  -o-border-radius: 25px;
  -ms-border-radius: 25px;
  -moz-border-radius: 25px;
  -webkit-border-radius: 25px;
  width: 250px;
  box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
  -o-box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
  -ms-box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
  -moz-box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
  -webkit-box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
  border: none;
  margin: 20px 0 73px 35px;
  cursor: pointer;
  font-family: 'Nunito', sans-serif;
  color: #fff;
  font-weight: 700;
  font-size: 16px;
}
.form-v9-content .form-detail .register:hover {
  background: #72B894;
}
.form-v9-content .form-detail .form-row-last input {
  padding: 14px;
}
input::-webkit-input-placeholder { /* Chrome/Opera/Safari */
  color: #e5e5e5;
  font-size: 16px;
}
input::-moz-placeholder { /* Firefox 19+ */
  color: #e5e5e5;
  font-size: 16px;
}
input:-ms-input-placeholder { /* IE 10+ */
  color: #e5e5e5;
  font-size: 16px;
}
input:-moz-placeholder { /* Firefox 18- */
  color: #e5e5e5;
  font-size: 16px;
}
.footer {
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
  background-color: #293F85;
  color: #FFFFFF;
  text-align: center;
}

/* Responsive */
@media screen and (max-width: 1199px) {
  .form-v9-content {
    margin: 185px 20px;
  }
}
@media screen and (max-width: 767px) {
  .form-v9-content .form-row-total {
    flex-direction: column;
    -o-flex-direction: column;
    -ms-flex-direction: column;
    -moz-flex-direction: column;
    -webkit-flex-direction: column;
  }
  .form-v9-content .form-row {
    width: 100%;
  }
}
@media screen and (max-width: 575px) {
  .form-v9-content .form-detail {
    padding: 30px 45px 30px 10px;
  }
}
